import { Layout, Menu } from "antd";

import { UserOutlined, LaptopOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";

const { SubMenu } = Menu;
const { Sider } = Layout;

const subMenu = [
  {
    key: "tools",
    icon: <LaptopOutlined />,
    title: "",
    menuItems: [
      {
        key: "randomNumber",
        path: "/randomNumber",
        title: "",
      },
      {
        key: "randomName",
        path: "/randomName",
        title: "",
      },
      {
        key: "turntable",
        path: "/turntable",
        title: "",
      },
      {
        key: "dice",
        path: "/dice",
        title: "",
      },
    ],
  },
  {
    key: "docs",
    icon: <UserOutlined />,
    title: "",
    menuItems: [
      {
        key: "excel",
        path: "/excel",
        title: "Excel ",
      },
      {
        key: "word",
        path: "/word",
        title: "Word ",
      },
    ],
  },
];

const AppSider = () => {
  const keyPathString = localStorage.getItem("currentMenu") || "[]";
  const cachekeyPath = JSON.parse(keyPathString);
  const [keyPath, setKeyPath] = useState<string[]>(cachekeyPath);

  useEffect(() => {
    localStorage.setItem("currentMenu", JSON.stringify(keyPath));
  }, [keyPath]);

  const clickSubMenu = (e: { keyPath: string[] }) => {
    setKeyPath(e.keyPath);
  };

  return (
    <Sider width={200} className="site-layout-background">
      <Menu
        mode="inline"
        defaultOpenKeys={keyPath.length > 0 ? keyPath : [subMenu[0].key]}
        defaultSelectedKeys={
          keyPath.length > 0 ? keyPath : [subMenu[0].menuItems[0].key]
        }
        style={{ height: "100%", borderRight: 0 }}
        onSelect={clickSubMenu}
      >
        {subMenu.map((item) => {
          const { key, icon, title, menuItems } = item;
          return (
            <SubMenu key={key} icon={icon} title={title}>
              {menuItems.map((subItem) => {
                const { key, path, title } = subItem;
                return (
                  <Menu.Item key={key}>
                    <Link to={path}>{title}</Link>
                  </Menu.Item>
                );
              })}
            </SubMenu>
          );
        })}
      </Menu>
    </Sider>
  );
};

export default AppSider;
